<!-- 车辆管理 -->
<template>
	<div class="contain">
		<el-aside width="200px" class="side">
			<span class="icon iconfont menu-icon">&#xe603;</span>
			<div @click="clickMenuOpend" class="title"><span :class="menuOpend ? 'el-icon-caret-bottom' : 'el-icon-caret-top'"></span>车辆管理</div>
			<div class="menu-box">
				<transition name="test-fead">
					<ul v-if="menuOpend">
						<li @click="changeActive(index)" :class="{ active: curIndex == index }" v-for="(v,index) in menuList">
						<!-- <span v-if="index=='0'" class="icon iconfont">&#xe6a4;</span>
							<span v-if="index=='1'" style="font-size:14px;" class="icon iconfont">&#xe605;</span> -->
							<span v-if="index=='0'" class="icon iconfont">&#xe6e8;</span>
							<span v-if="index=='1'" class="icon iconfont">&#xe604;</span>
							{{ v.text }}
						</li>
					</ul>
				</transition>
			</div>

		</el-aside>
		<router-view></router-view>
	</div>
</template>

<script>
export default {
	name: 'carMonag',
	data() {
		return {
			menuList: [
			{
				text: '驾驶员管理'
			},
			{
				text: '车辆管理'
			}
			],
			menuOpend: true,
			downShow: true,
			lock: true,
			curIndex: '0'
		}
	},
	created() {

	},
	mounted() {
		
	},
	methods: {
		changeActive(index){
			this.curIndex = index;
			switch(index){
				case 0:
				this.$router.push('/home/carManag/driverManag');
				break;
				case 1:
				this.$router.push('/home/carManag/carManagSub');
				break;
				default:
			}
		},
		clickMenuOpend(){
			if(!this.lock){
				return;
			}
			this.lock = false;
			this.menuOpend = !this.menuOpend;
			setTimeout(()=>{
				this.lock = true;
			},500)
		}
	}
}
</script>

<style scoped>
/*v-enter：定义进入过渡的开始状态。在元素被插入之前生效，在元素被插入之后的下一帧移除。

v-enter-active：定义进入过渡生效时的状态。在整个进入过渡的阶段中应用，在元素被插入之前生效，在过渡/动画完成之后移除。这个类可以被用来定义进入过渡的过程时间，延迟和曲线函数。

v-enter-to: 2.1.8版及以上 定义进入过渡的结束状态。在元素被插入之后下一帧生效 (与此同时 v-enter 被移除)，在过渡/动画完成之后移除。

v-leave: 定义离开过渡的开始状态。在离开过渡被触发时立刻生效，下一帧被移除。

v-leave-active：定义离开过渡生效时的状态。在整个离开过渡的阶段中应用，在离开过渡被触发时立刻生效，在过渡/动画完成之后移除。这个类可以被用来定义离开过渡的过程时间，延迟和曲线函数。

v-leave-to: 2.1.8版及以上 定义离开过渡的结束状态。在离开过渡被触发之后下一帧生效 (与此同时 v-leave 被删除)，在过渡/动画完成之后移除。*/
.contain{display: flex;background: url(../../assets/image/carManag/sideBg.png) repeat-y;flex: 1;}
.contain .side{width: 200px;}
.side .menu-icon{font-size: 14px;color: #fff;text-align: center;background: #4a5064;height: 30px;line-height: 30px;display: block;}
.side .title{height: 40px;line-height: 40px;padding-left: 12px;color: #fff;font-size: 14px;background: #00a8d4;display: block;}
.side .title span{cursor: pointer;}
.side ul li{color: #fff;padding-left: 12px;height: 40px;line-height: 40px;cursor: pointer;}
.side ul li span{font-size: 16px;color: #9ca3ac;display: inline-block;width: 30px;}
.side ul .active{background: #42485b;}
.menu-box{overflow: hidden;}
.test-fead-enter-active,.test-fead-leave-active{
	transition: linear .5s;
}
.test-fead-enter,.test-fead-leave-to{margin-top: -100%;}
</style>


